___CSS_LOADER_EXPORT___.push([module.id, `/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Make body position relative for non-nested absolute positioned elements*/
body {
  position: relative;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul[role=list],
ol[role=list] {
  list-style: none;
  padding: 0;
  margin: 0;
}

/* Set smooth scrolling for inner navigation */
html {
  scroll-behavior: smooth;
}

/* Set core root defaults */
html:focus-within {
  scroll-behavior: smooth;
}

/* Set core body defaults */
/* svh(small viewport height) to avoid browser's UI crops,  falls back to 100vh*/
body {
  min-height: 100vh;
  min-height: 100svh;
  text-rendering: optimizeSpeed;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with > max-width 100% = img can't overflow container even if they have a bigger original size */
/* Use width: 100%  so img can grow with its container even if img original size is smaller than container*/
img,
picture {
  max-width: 100%;
  display: block;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Remove all animations, transitions and smooth scroll for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }
  *,
  *::before,
  *::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}`, "",{"version":3,"sources":["webpack://./src/reset.scss"],"names":[],"mappings":"AAAA,qBAAA;AACA;;;EAGE,sBAAA;AACF;;AAEA,2EAAA;AACA;EACE,kBAAA;AACF;;AAEA,0BAAA;AACA;;;;;;;;;;;;EAYE,SAAA;AACF;;AAEA,2GAAA;AACA;;EAEE,gBAAA;EACA,UAAA;EACA,SAAA;AACF;;AAEA,8CAAA;AACA;EACE,uBAAA;AACF;;AAEA,2BAAA;AACA;EACE,uBAAA;AACF;;AAEA,2BAAA;AACA,gFAAA;AACA;EACE,iBAAA;EACA,kBAAA;EACA,6BAAA;AACF;;AAEA,0DAAA;AACA;EACE,8BAAA;AACF;;AAEA,6HAAA;AACA,2GAAA;AACA;;EAEE,eAAA;EACA,cAAA;AACF;;AAEA,yCAAA;AACA;;;;EAIE,aAAA;AACF;;AAEA,gGAAA;AACA;EACE;IACC,qBAAA;EACD;EAEA;;;IAGE,qCAAA;IACA,uCAAA;IACA,sCAAA;IACA,gCAAA;EAAF;AACF","sourcesContent":["/* Box sizing rules */\r\n*,\r\n*::before,\r\n*::after {\r\n  box-sizing: border-box;\r\n}\r\n\r\n/* Make body position relative for non-nested absolute positioned elements*/\r\nbody {\r\n  position: relative;\r\n}\r\n\r\n/* Remove default margin */\r\nbody,\r\nh1,\r\nh2,\r\nh3,\r\nh4,\r\nh5,\r\nh6,\r\np,\r\nfigure,\r\nblockquote,\r\ndl,\r\ndd {\r\n  margin: 0;\r\n}\r\n\r\n/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */\r\nul[role='list'],\r\nol[role='list'] {\r\n  list-style: none;\r\n  padding: 0;\r\n  margin: 0;\r\n}\r\n\r\n/* Set smooth scrolling for inner navigation */\r\nhtml {\r\n  scroll-behavior: smooth;\r\n}\r\n\r\n/* Set core root defaults */\r\nhtml:focus-within {\r\n  scroll-behavior: smooth;\r\n}\r\n\r\n/* Set core body defaults */\r\n/* svh(small viewport height) to avoid browser's UI crops,  falls back to 100vh*/\r\nbody {\r\n  min-height: 100vh;\r\n  min-height: 100svh;\r\n  text-rendering: optimizeSpeed;\r\n}\r\n\r\n/* A elements that don't have a class get default styles */\r\na:not([class]) {\r\n  text-decoration-skip-ink: auto;\r\n}\r\n\r\n/* Make images easier to work with > max-width 100% = img can't overflow container even if they have a bigger original size */\r\n/* Use width: 100%  so img can grow with its container even if img original size is smaller than container*/\r\